import React, {useState} from 'react';
import { connect } from 'react-redux'
import { getPreviewIsLoading, randomizeFace, saveImage } from '../../reducers/preview'
import './button-actions.scss'
import { FaDownload, FaRandom } from 'react-icons/fa'

import DownloadSuccessModal from '../download-success-modal'
import DownloadModal from '../download-modal'


function ButtonActions(props) {

  let [modalSuccessIsOpen, setModalSuccessIsOpen] = useState(false);
  let [modalDownloadIsOpen, setModalDownloadIsOpen] = useState(false);
  let [imgBase64, setImgBase64] = useState(null);

  function onDownload() {
    props.saveImage(props.setName).then((response) => {
      var reader = new FileReader();
      reader.readAsDataURL(response.data);
      reader.onloadend = function() {
        let base64data = reader.result;
        setImgBase64(base64data);
        setModalSuccessIsOpen(true);
        setModalDownloadIsOpen(false);
      }
    })
  }

  return (
    <div className="ButtonActions">
      <button
        className="btn btn-primary btn-wide mb-2"
        onClick={props.randomizeFace}
        disabled={props.savingIsLoading || props.previewIsLoading}
      >
        <FaRandom />&nbsp;
        <span className="button-text">Random</span>
      </button>
      <br/>
      <button
        className="btn btn-primary btn-wide"
        onClick={() => setModalDownloadIsOpen(true)}
        disabled={props.savingIsLoading || props.previewIsLoading}
      >
        <FaDownload />&nbsp;
        <span className="button-text">Download</span>
      </button>
      <DownloadModal
        isOpen={modalDownloadIsOpen}
        closeModal={() => setModalDownloadIsOpen(false)}
        onDownload={onDownload}
      />
      <DownloadSuccessModal
        isOpen={modalSuccessIsOpen}
        closeModal={() => setModalSuccessIsOpen(false)}
        imgBase64={imgBase64}
      />
    </div>
  )
}



export default connect(
  state => ({
    savingIsLoading: state.savingIsLoading,
    previewIsLoading: state.facePartsAreLoading || getPreviewIsLoading(state)
  }),
  dispatch => ({
    randomizeFace: () => dispatch(randomizeFace()),
    saveImage: (setName) => dispatch(saveImage(setName))
  })
)(ButtonActions)
