import React from 'react';
import { connect } from 'react-redux'
import { getCurrentValues, getPreviewIsLoading } from '../../reducers/preview'
import Spinner from '../spinner'
import classNames from 'classnames'
import { Link } from "react-router-dom";
import { FaLevelUpAlt } from 'react-icons/fa'

import './preview.scss'

function Preview({ items, previewIsLoading, setName }) {
  return (
    <div className={classNames('Preview', previewIsLoading && 'isLoading')}>
      <div className="change-art">
        <Link to='../'>
          <button className="btn btn-info">
            <FaLevelUpAlt />&nbsp;
              Change Art Set
          </button>
        </Link>
      </div>

      <div className="images">
        {
          items.map((item, index) => (
            <div className="img" style={{backgroundImage: 'url(' + item + ')'}} key={index} alt=""/>
          ))
        }
      </div>
      <Spinner />
    </div>
  )
}


export default connect(
  state => ({
    items: getCurrentValues(state),
    current: state.current,
    previewIsLoading: state.facePartsAreLoading || getPreviewIsLoading(state) || state.savingIsLoading
  })
)(Preview)
