import React, { useState, useEffect } from 'react';
import { connect } from 'react-redux'
import { changeColor } from '../../reducers/preview'
import './color-picker.scss'
import { ChromePicker } from 'react-color'
import { ReactComponent as Pipette } from './pipette.svg'
import { FaEyeDropper } from 'react-icons/fa'


function ColorPicker({ part, changeColor, partsData, current }) {
  const [showPicker, setShowPicker] = useState(false)
  const [currentColor, setCurrentColor] = useState((current[part] && current[part].color) || '#ffffff');
  const colors = partsData[part].colors;

  const onBackgroundClick = () => {
    setShowPicker(false)
  }

  useEffect(() => {
    window.addEventListener('click', onBackgroundClick, false)
    return function cleanup() {
      window.removeEventListener('click', onBackgroundClick)
    }
  }, [])

  if (!colors || !colors.length) return null;

  return (
    <div className="ColorPicker" onClick={e => e.stopPropagation()}>
      <div
        className="color color-pipette"
        onClick={() => {
          setShowPicker(!showPicker)
        }}
        style={{backgroundColor: (current[part] && current[part].color) || currentColor}}
      >
        <FaEyeDropper/>
      </div>
      {colors.map(color => (
       <div className="color"
            style={{background: color}}
            key={color}
            onClick={() => changeColor(part, color)}>
       </div>
      ))}
      { showPicker &&
        <ChromePicker
          className="chromePicker"
          disableAlpha={true}
          color={(current[part] && current[part].color) || currentColor}

          onChange={(color) => {
            setCurrentColor(color.hex);
            changeColor(part, color.hex);
          }}
        />
      }
    </div>
  )
}


export default connect(
  state => ({
    partsData: state.partsData,
    current: state.current
  }),
  dispatch => ({
    changeColor: (part, color) => dispatch(changeColor(part, color)),
  })
)(ColorPicker)
