import React, {useEffect} from 'react';
import { connect } from 'react-redux'
import { getSets } from '../../reducers/preview'
import './social-login.scss'
import { Link } from "react-router-dom";
import { GoogleLogin } from 'react-google-login';
import FacebookLogin from 'react-facebook-login/dist/facebook-login-render-props';
import { TiSocialFacebookCircular } from 'react-icons/ti/';
import { login } from '../../reducers/preview';



function SocialLogin(props) {

  const onSuccessGoogleLogin = (res) => {
    //console.log(res.profileObj);
    props.login({ profile: res.profileObj });
  }

  const onSuccessFacebookLogin = (res) => {
    props.login({ profile: res });
  }

  return (
    <div className="SocialLogin">

      {props.profile ? (
        <div>
          You are logged in as {props.profile.name}
        </div>
      ) : (
        <div>
          <p>Sign in with a social network:</p>
          {/*<button onClick={onSuccessGoogleLogin}>Add</button>*/}
          <GoogleLogin
            clientId="546998968320-0cn7qut7aklej8akfljup6hi9a7najba.apps.googleusercontent.com"
            // autoLoad={true}
            buttonText="Google"
            onSuccess={onSuccessGoogleLogin}
            onFailure={(err) => console.log(err)}
          />
          <FacebookLogin
            appId="375214849866140"
            autoLoad={true}
            callback={onSuccessFacebookLogin}
            fields="name,email,picture"
            render={renderProps => (
              <button className="fb-button" onClick={renderProps.onClick}>
                <TiSocialFacebookCircular />
                Facebook
              </button>
            )}
          />
        </div>
      )}
    </div>
  )
}


export default connect(
  state => ({
    profile: state.profile
  }),
  {
    login
  }
)(SocialLogin)
